import Vue from 'vue'
import Vuex from 'vuex'
import storage from '../utils/storage'

Vue.use(Vuex);
// Vuex.Storetoken
const store = new Vuex.Store({

  state: {
    // token
    token:"",
    user:{} 
  },

  getters: {
	getToken(state){
		return state.token || storage.get("token") || "";
	}
  },

  mutations: {
    // tokentokenlocalStorage
    setToken(state,token) {
      state.token = token;
      storage.set('token', token);
      console.log('storelocalstoragetoken');
    },
    delToken(state) {
      state.token = "";
      storage.remove("token");
      console.log('storelocalstoragetoken');
    },
    setUser(state, user) {
      state.user = user;
      console.log('user',user);
    },
    delUser(state, user) {
      state.user = {};
      console.log('');
    },
    updateUser(state, user) {
      //  state
      state.user = user;
    }
  },

  actions: {
   // removeToken: (context) => {
     // context.commit('setToken')
   // }
   },
});
  
export default store;
